"use client";

import { useEffect, useMemo, useState } from "react";
import Modal from "@/components/admin/Modal";
import { API } from "@/lib/config";

const PAGE_SIZE = 20;

export default function ServiceTypeClient() {
  const [rows, setRows] = useState([]); // all items from API
  const [loading, setLoading] = useState(true);
  const [page, setPage] = useState(1);
  const [q, setQ] = useState("");

  // modal state
  const [open, setOpen] = useState(false);
  const [form, setForm] = useState({
    ServiceId: 0,
    ordernumber: "",
    title: "",
    file: null,
  });

  async function load() {
    setLoading(true);
    try {
      const res = await fetch(API.LIST, { cache: "no-store" });
      if (!res.ok) throw new Error("Failed to load list");
      const data = await res.json();

      const arr = Array.isArray(data?.GetAllService) ? data.GetAllService : [];

      const normalized = arr.map((r) => {
        const filename = r.coverimage ?? null; // API returns file name or null
        return {
          id: r.ServicesID,
          ServiceId: r.ServicesID,
          title: r.titile ?? "",
          ordernumber: r.ordernumber ?? 0,
          imageUrl: filename
            ? filename.startsWith("http")
              ? filename
              : `${IMAGE_BASE}${filename}`
            : null,
          _raw: r,
        };
      });

      setRows(normalized);
    } catch (e) {
      console.error(e);
      alert("Could not load services. Check API URL/CORS/HTTPS.");
    } finally {
      setLoading(false);
    }
  }
  async function uploadCover(servicesId, file) {
    const fd = new FormData();
    // Backend expects form field name "ServicesID" (case sensitive)
    fd.append("ServicesID", String(servicesId));
    // Any file key is accepted by your code (it iterates httpRequest.Files), use "file"
    fd.append("file", file);

    const res = await fetch(API.UPLOAD, {
      method: "POST",
      body: fd,
    });
    if (!res.ok) {
      const msg = await res.text();
      throw new Error(msg || "Image upload failed");
    }
    // Response shape per your code: { UploadDocuments: { error, message, Data } } (depending on Utility wrapper)
    return res.json().catch(() => ({}));
  }

  useEffect(() => {
    load();
  }, []);

  const filtered = useMemo(() => {
    if (!q) return rows;
    const qq = q.toLowerCase();
    return rows.filter((r) => (r.title || "").toLowerCase().includes(qq));
  }, [rows, q]);

  const totalPages = Math.max(1, Math.ceil(filtered.length / PAGE_SIZE));
  const pageSafe = Math.min(page, totalPages);
  const pageRows = filtered.slice(
    (pageSafe - 1) * PAGE_SIZE,
    pageSafe * PAGE_SIZE
  );

  function openNew() {
    setForm({ ServiceId: 0, ordernumber: "", title: "", file: null });
    setOpen(true);
  }

  function openEdit(r) {
    setForm({
      ServiceId: r.ServiceId || r.id, // ServicesID from API
      ordernumber: r.ordernumber ?? 0,
      title: r.title ?? "",
      file: null,
    });
    setOpen(true);
  }

  async function submit(e) {
    e.preventDefault();
    try {
      // 1) JSON upsert (add or edit). Your API expects titile + ordernumber + ServiceId
      const payload = {
        ServiceId: Number(form.ServiceId || 0),
        ordernumber: Number(form.ordernumber || 0),
        titile: form.title,
      };

      const upRes = await fetch(API.UPSERT, {
        method: "POST",
        headers: { "Content-Type": "application/json" },
        body: JSON.stringify(payload),
      });

      if (!upRes.ok) {
        const msg = await upRes.text();
        throw new Error(msg || "Save failed");
      }

      // Example response you showed:
      // { "ServiceAdd": { "error": false, "message": null, "Data": { "ServicesID": 7, ... } } }
      const upJson = await upRes.json().catch(() => ({}));
      const newId =
        upJson?.ServiceAdd?.Data?.ServicesID ??
        upJson?.ServicesID ??
        form.ServiceId; // fallback to existing id on edit

      // 2) If a file is selected, upload file to uploaddocuments with ServicesID
      if (form.file && newId) {
        await uploadCover(newId, form.file);
      }

      // 3) Close modal and refresh the list
      setOpen(false);
      await load();
    } catch (e) {
      console.error(e);
      alert(e.message || "Submit failed");
    }
  }

  async function onDelete(id) {
    if (!confirm("Delete this item?")) return;
    try {
      const res = await fetch(API.DELETE(id), { method: "DELETE" });
      if (!res.ok) {
        // Some .NET setups use GET for delete; fallback if 405/404.
        if (res.status === 405 || res.status === 404) {
          const r2 = await fetch(API.DELETE(id)); // GET fallback
          if (!r2.ok) throw new Error("Delete failed");
        } else {
          throw new Error("Delete failed");
        }
      }
      await load();
    } catch (e) {
      console.error(e);
      alert(e.message || "Delete failed (check endpoint)");
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex flex-col gap-3 sm:flex-row sm:items-center sm:justify-between">
        <h1 className="text-2xl md:text-3xl font-bold">Service Type</h1>
        <div className="flex gap-2">
          <input
            placeholder="Search title..."
            value={q}
            onChange={(e) => {
              setQ(e.target.value);
              setPage(1);
            }}
            className="w-full sm:w-64 rounded-xl border border-neutral-700 bg-neutral-800 px-3 py-2 outline-none focus:ring-2 focus:ring-emerald-500"
          />
          <button
            onClick={openNew}
            className="rounded-xl bg-emerald-600 hover:bg-emerald-500 px-4 py-2 font-medium"
          >
            + Add New
          </button>
        </div>
      </div>

      <div className="rounded-2xl border border-neutral-800 bg-neutral-900/50 overflow-x-auto">
        <table className="min-w-full text-sm">
          <thead>
            <tr className="text-left bg-neutral-900/70">
              <th className="px-4 py-3">#</th>
              <th className="px-4 py-3">Order</th>
              <th className="px-4 py-3">Title</th>
              <th className="px-4 py-3">Image</th>
              <th className="px-4 py-3 text-right">Actions</th>
            </tr>
          </thead>
          <tbody>
            {loading ? (
              <tr>
                <td className="px-4 py-6" colSpan={5}>
                  Loading…
                </td>
              </tr>
            ) : pageRows.length === 0 ? (
              <tr>
                <td className="px-4 py-6" colSpan={5}>
                  No records.
                </td>
              </tr>
            ) : (
              pageRows.map((r, idx) => (
                <tr
                  key={r.id || r.ServiceId}
                  className="border-t border-neutral-800"
                >
                  <td className="px-4 py-3">{(pageSafe - 1) * 20 + idx + 1}</td>
                  <td className="px-4 py-3">
                    {r.ordernumber ?? r.orderNumber ?? "-"}
                  </td>
                  <td className="px-4 py-3">{r.title ?? r.titile ?? "-"}</td>
                  <td className="px-4 py-3">
                    {r.imageUrl ? (
                      <img
                        src={r.imageUrl}
                        alt=""
                        className="h-10 w-16 object-cover rounded-lg border border-neutral-800"
                      />
                    ) : (
                      <span className="text-neutral-500">—</span>
                    )}
                  </td>
                  <td className="px-4 py-3 text-right">
                    <div className="inline-flex gap-2">
                      <button
                        onClick={() => openEdit(r)}
                        className="rounded-lg bg-neutral-800 hover:bg-neutral-700 px-3 py-1"
                      >
                        Edit
                      </button>
                      <button
                        onClick={() => onDelete(r.id)}
                        className="rounded-lg bg-red-600/80 hover:bg-red-600 px-3 py-1"
                      >
                        Delete
                      </button>
                    </div>
                  </td>
                </tr>
              ))
            )}
          </tbody>
        </table>
      </div>

      <div className="flex flex-col sm:flex-row items-center justify-between gap-3">
        <div className="text-sm text-neutral-400">
          Page {pageSafe} of {totalPages} • {filtered.length} total
        </div>
        <div className="flex gap-2">
          <button
            onClick={() => setPage((p) => Math.max(1, p - 1))}
            disabled={pageSafe <= 1}
            className="rounded-xl border border-neutral-700 px-3 py-1 disabled:opacity-50"
          >
            Prev
          </button>
          <button
            onClick={() => setPage((p) => Math.min(totalPages, p + 1))}
            disabled={pageSafe >= totalPages}
            className="rounded-xl border border-neutral-700 px-3 py-1 disabled:opacity-50"
          >
            Next
          </button>
        </div>
      </div>

      <Modal
        open={open}
        onClose={() => setOpen(false)}
        title={form.ServiceId ? "Edit Service Type" : "Add Service Type"}
      >
        <form onSubmit={submit} className="space-y-4">
          <input type="hidden" value={form.ServiceId} readOnly />

          <div className="grid gap-3 sm:grid-cols-2">
            <div>
              <label className="block text-sm text-neutral-300 mb-1">
                Order Number
              </label>
              <input
                required
                type="number"
                min="0"
                step="1"
                value={form.ordernumber}
                onChange={(e) =>
                  setForm({ ...form, ordernumber: e.target.value })
                }
                className="w-full rounded-xl border border-neutral-700 bg-neutral-800 px-3 py-2 outline-none focus:ring-2 focus:ring-emerald-500"
                placeholder="e.g. 2"
              />
            </div>
            <div>
              <label className="block text-sm text-neutral-300 mb-1">
                Title
              </label>
              <input
                required
                value={form.title}
                onChange={(e) => setForm({ ...form, title: e.target.value })}
                className="w-full rounded-xl border border-neutral-700 bg-neutral-800 px-3 py-2 outline-none focus:ring-2 focus:ring-emerald-500"
                placeholder="Example title"
              />
            </div>
          </div>

          <div>
            <label className="block text-sm text-neutral-300 mb-1">
              Image (one)
            </label>
            <input
              type="file"
              accept="image/*"
              onChange={(e) =>
                setForm({ ...form, file: e.target.files?.[0] || null })
              }
              className="block w-full text-sm text-neutral-300 file:mr-4 file:rounded-xl file:border-0 file:bg-neutral-800 file:px-3 file:py-2 file:text-neutral-200 hover:file:bg-neutral-700"
            />
            {form.file && (
              <p className="mt-1 text-xs text-neutral-500">
                (Note: current API doesn’t accept file upload on this endpoint.
                We’ll add an image upload route next.)
              </p>
            )}
          </div>

          <div className="flex justify-end gap-2">
            <button
              type="button"
              onClick={() => setOpen(false)}
              className="rounded-xl px-4 py-2 bg-neutral-800"
            >
              Cancel
            </button>
            <button className="rounded-xl px-4 py-2 bg-emerald-600 hover:bg-emerald-500 font-medium">
              Save
            </button>
          </div>
        </form>
      </Modal>
    </div>
  );
}
